body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.content_header {
  display: flex;
  justify-content: center; }

.avatar_header {
  margin: 0;
  padding: 0;
  width: 40px;
  height: 40px; }
  @media all and (min-width: 768px) {
    .avatar_header {
      width: 60px;
      height: 60px; } }
  @media all and (min-width: 1020px) {
    .avatar_header {
      width: 100px;
      height: 100px; } }

.title_header {
  text-align: center;
  font-family: 'Comic Sans MS', arial;
  font-size: 20px;
  text-shadow: 8px 8px 8px #aaa; }
  @media all and (min-width: 768px) {
    .title_header {
      font-size: 38px; } }
  @media all and (min-width: 1020px) {
    .title_header {
      font-size: 55px; } }

.style_input {
  border: 2px solid #d8643a;
  border-radius: 5px;
  height: 20px; }

.content_list {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center; }

.characters_list {
  list-style: none;
  display: grid;
  grid-template-columns: 200px;
  grid-template-rows: 1fr;
  grid-gap: 20px; }
  @media all and (min-width: 768px) {
    .characters_list {
      grid-template-columns: repeat(2, 200px);
      grid-template-rows: 1fr;
      grid-gap: 20px; } }
  @media all and (min-width: 1020px) {
    .characters_list {
      grid-template-columns: repeat(4, 200px);
      grid-template-rows: 1fr;
      grid-gap: 20px; } }

.characters_list-card {
  background-color: #d8643a;
  color: #FFFFFF;
  border: 2px solid #000000;
  height: 400px; }

.character_link {
  text-decoration: none;
  color: #FFFFFF; }

.content_detail-card {
  text-align: center;
  display: flex;
  justify-content: center; }

.link_return {
  margin-bottom: 30px; }

.card-image {
  width: 150px;
  height: 200px; }
  @media all and (min-width: 768px) {
    .card-image {
      width: 300px;
      height: 400px; } }
  @media all and (min-width: 1020px) {
    .card-image {
      width: 380px;
      height: 500px; } }

.character_card {
  background-color: #d8643a;
  color: #FFFFFF;
  width: 200px;
  height: 200px; }
  @media all and (min-width: 768px) {
    .character_card {
      width: 400px;
      height: 400px; } }
  @media all and (min-width: 1020px) {
    .character_card {
      width: 500px;
      height: 500px; } }

.character_detail-name {
  text-align: center;
  font-family: 'Comic Sans MS', arial;
  text-shadow: 8px 8px 8px #000000;
  margin-top: 20px;
  font-size: 18px; }
  @media all and (min-width: 768px) {
    .character_detail-name {
      font-size: 28px; } }
  @media all and (min-width: 1020px) {
    .character_detail-name {
      font-size: 32px; } }

.detail_card-list {
  margin-top: 10px;
  font-size: 12px;
  list-style: none;
  text-align: left; }
  @media all and (min-width: 768px) {
    .detail_card-list {
      font-size: 24px;
      line-height: 1; } }
  @media all and (min-width: 1020px) {
    .detail_card-list {
      font-size: 28px;
      line-height: 2; } }

.style_house {
  margin-left: 20px;
  display: flex;
  justify-content: space-around; }

.style_img-house {
  margin-top: 10px;
  width: 50px;
  height: 50px; }
  @media all and (min-width: 768px) {
    .style_img-house {
      width: 170px;
      height: 155px; } }

.characters_list-card-item {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 250px 100px 10px; }

.character_image {
  width: 200px;
  height: 270px; }

.character_name {
  font-family: 'Comic Sans MS', arial;
  margin-top: 30px; }

